.messenger{
    height: calc(100vh - 70px);
    display: flex;
}

.chatMenu{
    flex: 3.5;
}

.chatMenuInput{
    width: 90%;
    padding: 10px 0;
    border: none;
    border-bottom: 1px solid rgb(185, 183, 183);
}

.chatMenuInput:focus{
    outline: none;
}

.chatBox{
    flex: 5.5;
}

.chatOnline{
    flex: 3;
}

.chatMenuWrapper, .chatBoxWrapper, .chatOnlineWrapper{
    padding: 10px;
    height: 100%;
}

.chatBoxBottom{
    display: flex;
    margin-top: 5px;
    align-items: center;
    justify-content:space-between;
}

.chatMessageInput{
    width: 80%;
    height: 90px;
    padding: 10px;
}

.chatSubmitButton{
    width: 70px;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: teal;
    color: white;
    cursor: pointer;
}

.chatBoxWrapper{
    display: flex;
    flex-direction: column;
    position: relative;
}

.chatBoxTop{
    height: 100%;
    overflow-y: scroll;
    padding-right: 10px;
}

.noConversationText{
    position: absolute;
    top: 10%;
    font-size: 50px;
    color: rgb(219, 219, 219);
}

